<template lang="pug">
    .new__subject-content
        .subject__title Предмет
        form(@submit.prevent='saveSubject').new__subject
            .new__subject-points
                .form__row-subject
                    .form__item-subject
                        img(src='../../../assets/images/icons/mammal.svg').img-subject
                    .form__item-subject
                        label.label__name-subject Название
                            input(type='text' v-model='subject.name' required).new__subject-item
                    .form__item-subject
                        label.label__name-subject Код предмета
                            input(type='text' v-model='subject.code' required).new__subject-item
                    .form__item-subject
                        label.label__name-subject Департамент
                            select(v-model="subject.department.id" required).new__subject-item
                                option(value=0) Выбрать
                                option(:value='item.id' v-for='item in departments') {{item.name}}
                .buttons_new-subject
                    button(type='submit').save__button-subject Сохранить
                    button(@click='resetSubject()').reset__button-subject Отмена
            
</template>
<script>
import { mapActions} from "vuex";
export default {
    data() {
        return {
            subject: {
                code: '',
                name: '',
                department: {
                    id: 0
                }
            }
        }
    },
    props: {
        departments: Array
    },
    methods: {
        ...mapActions("subjects", ["addSubject"]),
        saveSubject() {
            if(this.subject.department.id !== 0) {
                this.addSubject(this.subject)
                this.$emit('eventNewSubject')
            }
        },
        resetSubject() {
            this.$emit('eventNewSubject')
        }
    }
}
</script>
